.header {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  height: 960px;
  width: 100vw;
  background: url(../img/football-back.png) no-repeat center top / cover;
}

.header-top__info {
  height: 35px;
  width: 100%;
  padding-top: 54px;
  display: flex;
  justify-content: space-between;
}

.logo {
  height: 33px;
  width: 190px;
  background: url("../img/SURFACE.png") no-repeat center / contain;
}

.header-about {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font: 14px / normal "ProximaNova";
  letter-spacing: 0.7px;
}

.header-about:hover {
  opacity: 0.8;
  transition: 0.2s ease-in-out;
}

.header-top-txt,
.header-bottom-txt {
  color: #ffffff;
  text-transform: uppercase;
  font: 90px / normal "ProximaNova Bl";
  letter-spacing: 4.2px;
}

.header-top-txt {
  width: fit-content;
  text-align: left;
  position: relative;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 285px 0 0 0;
}

.header-bottom-txt {
  line-height: 0.8;
  position: relative;
  text-align: right;
  margin: 0;
}

#line {
  height: 6px;
  width: 406px;
  color: #5e42b0;
  background-color: #5e42b0;
  -webkit-transform: rotate(-50deg);
  transform: rotate(-50deg);

  position: absolute;
  top: 405px;
  left: 488px;
  z-index: 80;
}

.menu {
  /*height: 20px;*/
  padding: 0;
  max-width: 567px;
  margin: 18px 0 0 auto;
  /*list-style: none;*/
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-left__container {
  /*height: 12px;*/
  width: 58.77193%;
  display: flex;
  justify-content: space-between;
}

.menu-right__container {
  /*height: 12px;*/
  width: 32.45614%;
  display: flex;
  justify-content: space-between;
}

.menu__item {
  text-decoration: none;
  color: #9071ea;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.7px;
  text-transform: uppercase;
}

.menu__item:hover {
  color: #ffffff;
  transition: 0.5s;
}

.btn-wrap {
  margin: 180px auto 0 auto;
  max-width: 672px;
  /*width: 90%;*/
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.app-btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 311px;
  min-width: 142px;
  height: 77px;
  transition: 0.5s;
  border-radius: 35px;
  background-color: #5e42b0;
  border: none;
  outline: none;
}

.app-btn:first-child {
  padding: 0 67px 0 43px;
}

.app-btn:nth-child(2) {
  padding: 0 85px 0 64px;
}

.app-btn:hover {
  box-shadow: 0px 16px 24px 0 #0a0e34;
  cursor: pointer;
}

.apple-icon {
  display: block;
  min-width: 33px;
  width: 33px;
  height: 37px;
  background: url("../img/Icon-Apple.png") no-repeat center / contain;
}

.app-btn__text {
  text-align: left;
  margin: 0;
  text-transform: uppercase;
  font: 14px / normal "ProximaNova";
  font-weight: 400;
  letter-spacing: 0.7px;
  color: #ffffff;
}

.app-text {
  display: block;
  font-weight: 700;
  line-height: 1.03;
}

.google-icon {
  min-width: 36px;
  width: 36px;
  height: 39px;
  background: url("../img/Icon-GooglePlay.png") no-repeat center / contain;
}

@media all and (max-width: 768px) {
  .header .wrapper {
    max-width: calc(768px - 20px - 20px);
  }

  .header-top-txt,
  .header-bottom-txt {
    font: bold 64px / normal "ProximaNova Bl";
    letter-spacing: 3.18px;
  }

  .header-top-txt {
    line-height: 0.49;
  }

  .header-bottom-txt {
    margin: 33px auto 0 auto;
    max-width: 490px;
    text-align: left;
  }

  .menu {
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
    max-width: 666px;
  }

  .menu-left__container {
    max-width: 378px;
    width: 100%;
  }

  .menu-right__container {
    max-width: 226px;
    width: 100%;
  }

  .menu__item {
    font: 16px / normal "ProximaNova";
    font-weight: 400;
    letter-spacing: 0.7px;
  }
}

@media all and (min-width: 425px) and (max-width: 670px) {
  .header-top-txt,
  .header-bottom-txt {
    font: 50px/ 0.97 "ProximaNova Bl";
    letter-spacing: 1.6px;
  }

  .header-about {
    display: none;
  }
  .header-top__info {
    justify-content: center;
  }

  .menu {
    flex-direction: column;
  }

  .app-btn {
    /*width: 142px;*/
    /*height: 39px;*/
  }

}
